import React from "react"
import PropTypes from "prop-types";
import { graphql } from "gatsby"
import Img from "gatsby-image"

import Layout from "components/layout"
import SEO from "components/seo"
import Card from "components/card"

export const queryImage = graphql`
    query {
        post1: file(relativePath: { eq: "gatsby-course.jpg" }) {
            childImageSharp {
                fixed(width: 96, height: 96) {
                    ...GatsbyImageSharpFixed
                }
            }
        }
    }
`;

const Blog = ({data}) => (
    <Layout>
        <SEO 
            pageTitle="Blog" 
            description="Desenvolvedor Web"
            keywords="Desenvolvedor, PHP, WEB, André, Silva, André Silva"
        />
        <section className="section is-size-4-desktop is-size-5-touch">
            <h1 className="title has-text-centered">Blog</h1>
            <hr/>
            <p className="has-text-centered">
                Siga-me no  
                {' '}
                <a rel="noopener noreferrer" href="https://twitter.com/AndreSilvaSI" target="_blank">Twitter</a>
            </p>

            <div className="columns is-centered">
                <div className="column is-half">
                    <Card 
                        title="Curso Gatsby"
                        subtitle="Aprendendo Gatsbyjs/react"
                        link=""
                        tags={["Gatsby","React"]}
                        image={<Img 
                            fixed={data.post1.childImageSharp.fixed}
                            alt="Curso Gatsby"
                        />}
                    />
                </div>
            </div>
        </section>
    </Layout>
)

Blog.propTypes = {
    data: PropTypes.object.isRequired
}

export default Blog